.footer {
  background-color: $secondary-bg-color;

  padding-top: 56px;

  padding-bottom: 25px;

  @media screen and (min-width: 768px) {
    padding-top: 80px;

    padding-bottom: 20px;
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 10px;
  }
}

.container__footer {
  margin-bottom: 45px;

  @media screen and (min-width: 768px) {
    margin-bottom: 90px;
    display: flex;
    justify-content: space-between;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 100px;
  }
}

.footer__widget {
  max-width: 232px;
  margin-bottom: 45px;

  @media screen and (min-width: 768px) {
    max-width: 340px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    max-width: 477px;
  }
}

.footer__title {
  font-family: Titan One;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.167;
  text-transform: uppercase;
  color: $subtitle-text-color;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.154;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.133;
  }
}

.footer__title-label {
  display: block;
  margin-top: 17px;

  font-family: Titan One;
  font-size: 34px;
  line-height: 1.147;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $color-accent;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.146;
    letter-spacing: 0.06em;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 15px;
    font-size: 58px;
    line-height: 1.139;
    letter-spacing: 0.06em;
  }
}

.footer__list {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 46px;
}

.footer__item {
  &:not(:last-child) {
    margin-right: 28px;

    @media screen and (min-width: 768px) {
      margin-right: 30px;
    }

    @media screen and (min-width: 1280px) {
      margin-right: 24px;
    }
  }
}

.footer__icon-link {
  display: flex;
  border-radius: 50%;
  background-color: $social-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $color-accent;
  }
}

.footer__icon {
  width: 32px;
  height: 32px;
  fill: $text-color;

  @media screen and (min-width: 768px) {
    width: 34px;
    height: 34px;
  }
}

.footer__text {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.86;
  text-align: right;
  color: $primary-text-color;

  @media screen and (min-width: 768px) {
    font-size: 14px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
}

.footer__contact-link {
  color: $color-accent;
}

.footer__copyright {
  border-top: 1px solid $reviews-line-color;
  padding-top: 22px;

  @media screen and (min-width: 768px) {
    padding-top: 29px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 10px;
  }
}

.text-copyright {
  width: 150px;
  margin-left: auto;
  margin-right: auto;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  color: $secondary-text-color;

  @media screen and (min-width: 768px) {
    width: 100%;

    line-height: 1.86;
  }
}
